* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Arial', sans-serif;
  }
body {
    background: url('4.png') no-repeat center center fixed;
    background-size: cover;
    overflow-x: hidden;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    min-height: 100vh;
    position: relative;
}

.container {
    width: 30%;
    background: rgba(255, 248, 220, 0.8);
    padding: 15px;
    margin-left: -34%;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-top: 30px;
    display: flex;
    position: fixed;
    justify-content: space-between;
    flex-wrap: wrap;
}
.image-upload-container {
  flex-direction: column;
  align-items: center;
  position: relative; 
  top: 20px; 
  right: -10px; 
  width: 290px;
  z-index: 10;
}
.image-box-container {
  background: rgba(255, 248, 220, 0.8);
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: grid;
  grid-template-columns: repeat(6, 106px); 
  grid-auto-rows: 105px;
  gap: 10px;
  overflow: auto;
  width: 750px;
  height: 250px;
  overflow: hidden;
}

.image-box {
  background-color: #f0f0f0;
  border: 2px dashed #ddd;
  border-radius: 10px;
  margin-top: 10px;
  width: 35px;
  height: 35px;
  margin-left: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.plus-sign {
  font-size: 2em;
  color: #888;
}

.uploaded-images {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 10px;
  margin-top: 70px;
  margin-left: -110px;
}

h2{
  margin-right: 140px;
  margin-top: 35px;
  font-size: 20px;
  margin-bottom: 20px;
  font-weight: normal;
}

.uploaded-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 8px;
}

.image-wrapper {
  position: relative;
  display: inline-block;
  margin: 10px;
}

.uploaded-image {
  max-width: 85px;
  max-height: 85px;
  object-fit: cover;
  border-radius: 4px;
}

.delete-image {
  position: absolute;
  top: 5px;
  right: 5px;
  background: rgba(255, 255, 255, 0.8);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 18px;
  color: red;
}

.delete-image:hover {
  background: rgba(255, 255, 255, 1);
}

h1 {
    width: 100%;
    text-align: center;
    font-weight: 500;
    font-size: 35px;
    color: #36454F;
    margin: 20px 0;
    text-transform: uppercase;
  }
.add-task {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}
#task-input {
    width: 60%;
    font-size: 15px;
    padding: 15px;
    border: none;
    background: #FFF8DC;
    color: #333;
    border-radius: 6px;
    outline: none;
    margin-right: 10px;
}
#task-input::placeholder {
    color: #aaa;
}
#task-input:focus {
    box-shadow: 0 0 5px rgba(0,0,0,0.3);
}
button[type="submit"] {
    background: #FFAE42;
    color: #fff;
    border: none;
    padding: 12px 20px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 6px;
    transition: background 0.3s ease;
    outline: none;
  }
  button[type="submit"]:hover {
    background: #FF8C00;
  }
  .selection-holder {
    width: 100%;
    text-align: center;
    margin-bottom: 20px;
  }
  #filter-tasks {
    padding: 10px 15px;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    background: #FFF8DC;
    color: #FFAE42;
    outline: none;
  }
  .task-heading {
    width: 100%;
    font-size: 21px;
    color: #36454F;
    font-weight: 500;
    margin: 10px;
    margin-left: 90px;
    text-align: center;
  }
  .task-headings {
      margin-left: -30px;
      width: 100%;
      text-align: center;
      font-weight: 500;
      font-size: 22px;
      color: #36454F;
      margin: 20px 0;
      text-transform: uppercase;
    }
  .task-container {
    list-style: none;
    padding: 0;
    width: 100%;
  }
  .task-item {
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #CEF6F5;
  }
  .task-text {
    flex: 1;
    font-size: 18px;
    color: #333;
    font-weight: 500;
    border: none;
    background: transparent;
    outline: none;
  }
  .done-input {
    text-decoration: line-through;
    opacity: 0.5;
  }
  .task-item button {
    background-color: transparent;
    color: #333;
    font-size: 16px;
    font-weight: 500;
    border: none;
    cursor: pointer;
    transition: color 0.2s;
    margin-left: 10px;
  }
  .task-item button:hover {
    color: #FF8C00;
  }
  .task-item:nth-child(even) {
    background-color: #F7D6E0;
}
.calendar-container {
    position: relative; 
    top: 27px; 
    right: -10px; 
    width: 150px; 
    text-align: center;
    z-index: 0;
  }
.calendar {
    border-collapse: collapse;
    width: 100%;
    font-size: 14px;
    margin-top: 20px;
    border-radius: 10px; 
    overflow: hidden; 
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #FFF8DC;
}
.calendar th, .calendar td {
    padding: 10px;
    text-align: center;
    border: none;
  }
  .calendar th {
    background-color: #FFAE42;
    color: #fff;
    padding: 12px;
    font-weight: bold;
  }
  .calendar td {
    background-color: #FFF8DC;
    cursor: pointer;
    transition: background-color 0.3s ease;
    color: #555;
  }
  .calendar td.today {
    background-color: #FFD700;
    border-radius: 50px;
  }
  .calendar td:hover {
    background-color: #FF8C00;
  }
.sticky-note {
    position: fixed;
    top: 85px;
    right: 400px;
    width: 320px; 
    height: 250px;
    resize: vertical;
    min-height: 100px;
    background: #FFFAF0;
    border: 1px solid #FFDAB9;
    border-left: 8px solid #FFA07A;
    border-radius: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    overflow: hidden;
}

  .sticky-note textarea {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    font-size: 16px;
    color: #333;
    outline: none;
    resize: none;
    font-family: 'Arial', sans-serif;
    padding: 10px;
    line-height: 1.6;
  }

  .sticky-note::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 15px;
    background: #FFA07A;
    border-radius: 10px 10px 0 0;
  }

  .sticky-note::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 20px;
    height: 20px;
    background: #eaad95;
    clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}
  
